* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Barlow", sans-serif;
}

::-webkit-scrollbar {
    background-color: black;
}

::-webkit-scrollbar-corner {
    background-color: black;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(100, 100, 100);
}
